import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect  
} from "react-router-dom";

import NotFlixHeader from './NotFlixHeader/NotFlixHeader.js';
import MovieCatalog from './MovieCatalog/MovieCatalog.js';

import ContextProvider from './ContextProvider/ContextProvider.js';
import { MContext } from './ContextProvider/ContextProvider.js';
import RegisterPage from './Register/Register'
import LoginPage from './Login/Login'
import MovieDetails from './MovieDetails/movieDetails.js'
import ScrollToTop from './ScrollToTop/ScrollToTop.js'

function App() {
  return (
    <div className="App">
      <ContextProvider>
      <Router>
        <NotFlixHeader />           
        <div className="container general">
        <ScrollToTop>       
        <Switch>
          <Route exact path={["/",""]} component={MovieCatalog} />
          <Route path="/movie/:id" render={(props) => <MovieDetails {...props} />}/> 
          <Route path="/favourites">
            <MContext.Consumer>{(context) => (                       
              (context.state.username!==null)? <MovieCatalog isFavorites={true}/>:<Redirect to="/"/>
            )}</MContext.Consumer>        
          </Route>
          <Route path="/login">
            <LoginPage />
          </Route>
          <Route path="/register">
            <RegisterPage />
          </Route>
          <Redirect to="/" />
        </Switch>
        </ScrollToTop>      
        </div>
        <footer></footer>    
      </Router>  
      </ContextProvider>
    </div>    
  );
}
export default App;

