import './Register.css';
import React from 'react';
import Axios from 'axios';
import {withRouter} from 'react-router-dom'
import { MContext } from './../ContextProvider/ContextProvider';

class Register extends React.PureComponent {

    constructor() {
        super();
        this.state = {
            form: {
                username: null,
                name: null,
                surname: null,
                email: null,
                password: null,
                passConf: null
            },
            validationErrors: {
                username: false,
                usernameError: null,
                name: false,
                nameError: null,
                surname: false,
                surnameError: null,
                email: false,
                emailError: null,
                password: false,
                passwordError: null,
                passConf: false,
                passConfError: null
            },
            serverError: {
                hasError: false,
                errorMessage: null
            }
        }
    }

    handleValidation() {
        let fields = this.state.form;
        let errors = {};
        let formIsValid = true;

        if(!fields.username) {
            formIsValid = false;
            errors.username = true;
            errors.usernameError = "Username is required"
        }

        if(!fields.name) {
            formIsValid = false;
            errors.name = true;
            errors.nameError = "Name is required"
        }

        if(!fields.surname) {
            formIsValid = false;
            errors.surname = true;
            errors.surnameError = "Surname is required"
        }

        if(!fields.email ||
           !fields.email.match(/^[a-zA-Z.]+@[a-zA-Z]+.[a-zA-Z]+$/))
        {
            formIsValid = false;
            errors.email = true;
            errors.emailError = "Invalid email"
        }

        if(!fields.password) {
            formIsValid = false;
            errors.password = true;
            errors.passwordError = "Password is required"
        }

        if(!fields.passConf ||
           (fields.password !== fields.passConf)) 
        {
            formIsValid = false;
            errors.passConf = true;
            errors.passConfError = "Passwords don't match"
        }

        this.setState({validationErrors: errors});
        return formIsValid;
    }

    async handleSubmit(event) {
        event.preventDefault();

        this.setState({validationErrors: {}, serverError: {}});
        if(this.handleValidation()) {
            let formData = { ...this.state.form };
            delete formData.passConf;
            Axios
                .post("http://localhost:5000/notflix/users", formData)
                .then(res => {
                    this.context.setStateByName("username", this.state.form.username);
                    localStorage.setItem('notflix_token', res.data.token );
                    this.props.history.push("/");
                })
                .catch(err => {
                    let message;
                    switch(err.response.status) {
                        case 500:
                            message = "Internal server error";
                            break;
                        default:
                            message = "Sorry, there was a problem";
                            break;
                    }

                    this.setState({
                        serverError: {
                            hasError: true,
                            errorMessage: message
                        }
                    });
                });
        }
    }

    handleFormChange(field, event) {
        let form = this.state.form;
        form[field] = event.target.value.trim();
        this.setState({form: form});
    }

    render() {
        return<div id="signup">
            <div className="card">
                <div className="card-body">
                    <form className="form-signup">
                        <h1 className="h3 mb-3 font-weight-normal">Create Account</h1>

                        <div className={`alert alert-danger ${this.state.serverError.hasError ? '' : 'd-none'}`} role="alert">
                            {this.state.serverError.errorMessage}
                        </div>

                        <label htmlFor="inputUsername" className="sr-only">Username</label>
                        <input type="text" id="inputUsername" className={`form-control ${this.state.validationErrors.username ? "is-invalid" : ""}`} placeholder="Username" required onChange={(event) => this.handleFormChange("username" ,event)}/>
                        <div className="invalid-feedback">
                            {this.state.validationErrors.usernameError}
                        </div>

                        <div className="form-row">
                            <div className="col">
                                <label htmlFor="inputName" className="sr-only">Name</label>
                                <input type="text" id="inputName" className={`form-control ${this.state.validationErrors.name ? "is-invalid" : ""}`} placeholder="Name" required onChange={(event) => this.handleFormChange("name" ,event)}/>
                                <div className="invalid-feedback">
                                {this.state.validationErrors.nameError}
                                </div>
                            </div>
                            <div className="col">
                                <label htmlFor="inputSurname" className="sr-only">Surname</label>
                                <input type="text" id="inputSurname" className={`form-control ${this.state.validationErrors.surname ? "is-invalid" : ""}`} placeholder="Surname" required onChange={(event) => this.handleFormChange("surname" ,event)}/>  
                                <div className="invalid-feedback">
                                {this.state.validationErrors.surnameError}
                                </div>
                            </div>
                        </div>

                        <label htmlFor="inputEmail" className="sr-only">Email address</label>
                        <input type="email" id="inputEmail" className={`form-control ${this.state.validationErrors.email ? "is-invalid" : ""}`} placeholder="Email address" required onChange={(event) => this.handleFormChange("email" ,event)}/>
                        <div className="invalid-feedback">
                            {this.state.validationErrors.emailError}
                        </div>

                        <label htmlFor="inputPassword" className="sr-only">Password</label>
                        <input type="password" id="inputPassword" className={`form-control ${this.state.validationErrors.password ? "is-invalid" : ""}`} placeholder="Password" required onChange={(event) => this.handleFormChange("password" ,event)}/>
                        <div className="invalid-feedback">
                            {this.state.validationErrors.passwordError}
                        </div>   

                        <label htmlFor="confirmPassword" className="sr-only">Confirm Password</label>
                        <input type="password" id="confirmPassword" className={`form-control ${this.state.validationErrors.passConf ? "is-invalid" : ""}`} placeholder="Confirm Password" required onChange={(event) => this.handleFormChange("passConf" ,event)}/>
                        <div className="invalid-feedback">
                            {this.state.validationErrors.passConfError}
                        </div>
                        
                        <button className="btn btn-success btn-block" type="submit" onClick={(event) => this.handleSubmit(event)}>Signup</button>
                    </form>
                </div>
            </div>
        </div>
    }
}
Register.contextType = MContext;
export default withRouter(Register);