import './NotFlixHeader.css';
import React from 'react';

import {Link, withRouter} from "react-router-dom";

import CategoryDropDown from '../CategoryDropDown/CategoryDropDown.js';
import SearchBar from '../SearchBar/SearchBar.js';
import { MContext } from '../ContextProvider/ContextProvider.js';

import notflixLogo from '../Images/notflix-logo.png';
class NotFlixHeader extends React.PureComponent {

    componentDidUpdate(prevProps) {        
        if(this.props.location !== prevProps.location) {
            const path=this.props.location.pathname;            
            let b=['/'].includes(path);
            this.setState({isVisible:b});            
        }
    }

    constructor() {
        super();
        this.state = {isVisible:true}
        
    }
    async componentDidMount() {
        if(localStorage.getItem('notflix_token')) {
            var obj = atob(localStorage.getItem('notflix_token').split('.')[1]);
            let user = (JSON.parse(obj))
            this.context.setStateByName("username", user.username);
            
        }
        else this.context.setStateByName("username", "");
    }

    reloadAllMovies(){        
        
        this.context.setStateByName("selectedCategories",[]);
        this.context.setStateByName("reloadAll",true);                
    }

    logout() {

        this.context.setStateByName("recommendedMovies",[]);       
        localStorage.removeItem('notflix_token');
        this.context.setStateByName("username", "");

        this.props.history.push("/")
    }
    
    render() {
        return(
        <header>
            <nav className="navbar navbar-expand-lg navbar-light">
                <Link className="navbar-brand" to="/" onClick={() => this.reloadAllMovies()}>
                    <img id="logo-img" src={notflixLogo} alt=""/>                    
                    <span>NotFlix</span>   
                </Link>
            {this.state.isVisible?                                
                <div className="navbar-nav w-75 mx-auto">                       
                    <CategoryDropDown/>  
                    <SearchBar/>                   
                </div>
                :<div className="navbar-nav w-75 mx-auto"></div>}    

            {this.context.state.username!==""&&this.context.state.username!==null?
                <div className="favourite w-25 d-flex justify-content-start align-items-start"> 
                    <Link to="/favourites" className="favourite-link d-flex mr-auto align-items-center">                                                                      
                        <div className="star-icon"></div>
                        <span>Favourites</span>                                                                        
                    </Link>
                    <div className="profile-wrapper d-flex mr-auto align-items-center">
                        <div className="profile-photo"></div>
                        <div className="user-info-wrapper">
                            <p>Welcome Back!</p>                            
                            <p>{this.context.state.username}</p>
                            <p id="logout" onClick={(e) => this.logout(e)}>Logout</p>
                        </div>         
                    </div>                      
                </div>                
            :                        
                <div className="credential w-25 d-flex justify-content-center">
                    <Link to="/login">
                        <button className="btn login my-2 my-sm-0">Login</button>
                    </Link>                    
                    <Link to="/register">
                        <button className="btn register my-2 my-sm-0">Register</button>
                    </Link>
                </div>
            } 
            </nav>           
        </header>
    )}
}
NotFlixHeader.contextType = MContext;
export default withRouter(NotFlixHeader);