import '../css/bootstrap.min.css';
import './movieDetails.css';
import React from 'react';
import axios from 'axios';
import Rating from '@material-ui/lab/Rating';

import { MContext } from '../ContextProvider/ContextProvider.js';


export default class MovieDetails extends React.PureComponent {

    constructor() {
        super();
        this.state = {
            IdMovie: null,
            Title: null,
            Year: null,
            Duration: null,
            Price: null,
            ThumbnailURL: null,
            MovieURL: null,
            Description: null,
            MeanRating: null,
            categories: [],
            actors: [],
            reviews: [],
            review: "",
            myrating: 1,
            episodes: []
        }
    }
    async loadMovie() {
        if(this.context.state.username) {
            await axios.put('http://localhost:5000/notflix/'+this.props.match.params.id+'/user',{hasReadDetails: 1} ,{
                headers: {
                    authorization: "Bearer " + localStorage.getItem('notflix_token'),
                    'Content-Type': 'application/json',
                }            
            });
            var response = await axios.get('http://localhost:5000/notflix/'+this.props.match.params.id + '/user', {
                headers: {
                    authorization: "Bearer " + localStorage.getItem('notflix_token'),
                    'Content-Type': 'application/json',
                }            
            });
            this.setState({
                isBought: response.data[0].isBought,
                isFavourite: response.data[0].isFavourite,
            })
        }
        else 
            var response = await axios.get('http://localhost:5000/notflix/'+this.props.match.params.id, {
            headers: {
                'Content-Type': 'application/json',
            }            
        }); 
        this.setState({ 
            IdMovie: response.data[0].IdMovie,
            Title: response.data[0].Title,
            Year: response.data[0].Year,
            Duration: response.data[0].Duration,
            Price: response.data[0].Price,
            ThumbnailURL: response.data[0].ThumbnailURL,
            MovieURL: response.data[0].MovieURL,
            Description: response.data[0].Description,
            MeanRating: response.data[0].MeanRating,
        });
        if(this.state.MovieURL!==null) {
            let url = this.state.MovieURL;
            url = url.replace("watch?v=", "embed/");
            this.setState({MovieURL: url})
        }
    }

    async loadCategories(){
        let response = await axios.get('http://localhost:5000/notflix/'+this.props.match.params.id+"/categories", {
            headers: {
                'Content-Type': 'application/json',
            }            
        });
        let array = [];
        this.setState({
            categories: array
        })
        response.data.map(elem => array.push(elem.Name));
        if(array.find(function(elem) {
            return elem==="TV Show";
            })!==undefined) {
            this.loadEpisodes();
            }
    }

    async loadEpisodes(){
        let response = await axios.get('http://localhost:5000/notflix/'+this.props.match.params.id+"/episodes", {
            headers: {
                'Content-Type': 'application/json',
            }            
            });
            this.setState({episodes: response.data});
    }

    async loadActors(){
        let array=[];
        let response = await axios.get('http://localhost:5000/notflix/'+this.props.match.params.id+"/actors", {
            headers: {
                'Content-Type': 'application/json',
            }            
        });
        response.data.map(elem => array.push(elem.Name + " " + elem.Surname));
        this.setState({
            actors: array
        })
    }

    async loadReviews(){
        let response = await axios.get('http://localhost:5000/notflix/'+this.props.match.params.id+"/reviews", {
            headers: {
                'Content-Type': 'application/json',
            }            
        });
        this.setState({
            reviews: response.data
        })
    }

    async componentDidMount() {
        this.loadMovie();        
        this.loadCategories();               
        this.loadActors();
        this.loadReviews();        
       
    }

    async sendReview(event) {
        if(this.state.review!=="")await axios.put('http://localhost:5000/notflix/'+this.props.match.params.id+'/user',{Review: this.state.review} ,{
            headers: {
                authorization: "Bearer " + localStorage.getItem('notflix_token'),
                'Content-Type': 'application/json',
            }            
        });
        await axios.put('http://localhost:5000/notflix/'+this.props.match.params.id+'/user',{Rating: this.state.myrating} ,{
            headers: {
                authorization: "Bearer " + localStorage.getItem('notflix_token'),
                'Content-Type': 'application/json',
            }            
        });
        this.setState({review: ""})
        this.loadMovie();
        this.loadReviews();
    }    

    
    changeReview(event){
    event.preventDefault();
    this.setState({ review: event.target.value })
    }

    changeRating(event, value){
        event.preventDefault();
        this.setState({ myrating: value })       
    }

    async infoChange(event, parameter, value) {
        event.preventDefault();
        
        if(parameter==="isBought") {
            await axios.put('http://localhost:5000/notflix/'+this.props.match.params.id+'/user',{isBought: value} ,{
                headers: {
                    authorization: "Bearer " + localStorage.getItem('notflix_token'),
                    'Content-Type': 'application/json',
                }            
            });
            this.setState({ isBought: value})
        }
        else {
            await axios.put('http://localhost:5000/notflix/'+this.props.match.params.id+'/user',{isFavourite: value} ,{
                headers: {
                    authorization: "Bearer " + localStorage.getItem('notflix_token'),
                    'Content-Type': 'application/json',
                }            
            });
            this.setState({ isFavourite: value})
        }
    }

    render() {
        
        if(this.state.categories.find(function(elem) {
            return elem==="TV Show";
        })===undefined) {
        return(
            <div className="container-movie-details">
				<div className="movie-media-wrapper">
                {this.state.isBought?					
                	<iframe id="movie-iframe" title={this.state.Title} width="560" height="315" src={this.state.MovieURL} allowFullScreen></iframe>
                	:					
                	<img className="movie-thumbnail-detailed" alt="" src={this.state.ThumbnailURL} width="560" height="315"></img>               					
                }</div>								
                <div className="container">
                    <div className="row main">
                        <div className="col-sm main">
                            <div className="card text-white bg-dark mb-3">
                                <div className="card-body">
                                    <h5 className="card-title">{this.state.Title}</h5>
                                    <div className="col-sm">
                                        <Rating size="small" className="movie-rating" name="doesRating" precision={0.5} value={this.state.MeanRating} disabled={this.state.MeanRating===null} readOnly />  
                                    </div>
                                    <h6 className="card-subtitle mb-2 text-muted">Anno di uscita: {this.state.Year}, Durata: {this.state.Duration}''</h6>
                                    <p className="card-text">{this.state.Description}</p>
                                    <p className="card-text">{this.state.Price}	&euro;</p>
                                </div>
                                {this.context.state.username?
                                    <div className="card-body"> 
                                        {!this.state.isBought?
                                            <a href="#A" onClick={(e) => this.infoChange(e, "isBought", 1)} className="card-link">Buy Now!</a>
                                        : null
                                        }
                                        {!this.state.isFavourite?
                                            <a href="#A" onClick={(e) => this.infoChange(e, "isFavourite", 1)} className="card-link">Add to favourites</a>
                                        :
                                            <a href="#A" onClick={(e) => this.infoChange(e, "isFavourite", 0)} className="card-link">Remove from favourites</a>
                                        }
                                        
                                    </div>
                                :null
                                }
                            </div>
                        </div>
                        <div className="col-sm main">
							<div>
								<div className="row">
                        	        <div className="col-sm">
                        	            Attori
                        	        </div>
                        	    </div>
                        	    <div className="row">
                        	        <div className="col-sm">
                        	            <ul className="list-group">
                        	                    {this.state.actors.map(item => (
                        	                        <li className="list-group-item list-group-item-dark" key={item}>{item}</li>
                        	                    ))}
                        	            </ul>
                        	        </div>
                        	    </div>
                        	    <div className="row">
                        	        <div className="col-sm">
                        	            Categorie
                        	        </div>
                        	    </div>
                        	    <div className="row">
                        	        <div className="col-sm">
                        	            <ul className="list-group">
                        	                    {this.state.categories.map(item => (
                        	                        <li className="list-group-item list-group-item-dark" key={item}>{item}</li>
                        	                    ))}
                        	            </ul>
                        	        </div>
                        	    </div>
							</div>
						</div>
                    </div>
                </div>
                <div className="container reviews">
                        <ul className="list-group mt-n0 ">
                            {this.context.state.username?
                                <li className="list-group-item list-group-item-dark">
                                
                                    <div className="col-sm user-rates">
										<span>{this.context.state.username}</span>
                                        <Rating id="rate" size="small" className="movie-rating" name="doesRating" value={this.state.myrating} onChange={(event, newValue) => {this.changeRating(event, newValue);}} />  
                                    </div>
                                    
                                </li>
                            :null}
                            {this.context.state.username?
                            <li className="list-group-item mt-n3 list-group-item-dark">
                                <div className="input-group">
                                <textarea className="form-control" aria-label="With textarea" placeholder="Write your review!" value={this.state.review} onChange={(event => this.changeReview(event))}></textarea>
                                </div>
                            </li>
                            :null}
                            {this.context.state.username?
                            <li className="list-group-item mt-n3 list-group-item-dark">
                                <button id="sendRev" type="button" className="btn btn-primary btn-sm" onClick={(e) => this.sendReview(e)}>Send</button>
                            </li>  
                            :null}
                        {this.state.reviews.map(item => ( 
						<div key={"review"+item.fkUsers}>                            
                                <li className="commento list-group-item list-group-item-dark">
									<p>
										<span>{item.fkUsers}</span>
										<Rating size="small" className="movie-rating" name="doesRating" value={item.Rating} disabled={item.rating===null} readOnly/>
									</p>
                                <span>{item.Review}</span></li>
                        </div>                                                
                                            ))}
                    </ul>
                </div>
            </div>      
        )} else return 	(
                <div>
                    <div className="card text-white bg-dark mb-3">
                        <div className="row no-gutters">
                            <div className="col-md-4">
                            <img src={this.state.ThumbnailURL} className="card-img" alt="..."/>
                            </div>
                            <div className="col-md-8">
                                <div className="card-body">
                                    <h5 className="card-title">{this.state.Title}</h5>
                                    <p className="card-text">{this.state.Description}</p>
                                    <Rating size="small" className="movie-rating" name="doesRating" precision={0.5} value={this.state.MeanRating} disabled={this.state.MeanRating===null} readOnly />
                                    <p className="card-text">{this.state.Price}	&euro;</p>
                                    {this.context.state.username?
                                        <div className="card-body"> 
                                            {!this.state.isBought?
                                                <a href="#A" onClick={(e) => this.infoChange(e, "isBought", 1)} className="card-link">Buy Now!</a>
                                            : null
                                            }
                                            {!this.state.isFavourite?
                                                <a href="#A" onClick={(e) => this.infoChange(e, "isFavourite", 1)} className="card-link">Add to favourites</a>
                                            :
                                                <a href="#A" onClick={(e) => this.infoChange(e, "isFavourite", 0)} className="card-link">Remove from favourites</a>
                                            }
                                            
                                        </div>
                                    :null
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                    {this.state.episodes.map(item => (
                                                <li className="list-group-item list-group-item-dark" key={"episode-"+item.EpisodeNumber.toString()}>
                                                    <div className="card text-white bg-dark mb-3">
                                                    <div className="row no-gutters">
                                                        <div className="col-md-4">
                                                        <img src={item.ThumbnailURL} className="card-img" alt="Film thumbnail"/>
                                                        </div>
                                                        <div className="col-md-8">
                                                            <div className="card-body">
                                                                <h5 className="card-title">Episodio numero: {item.EpisodeNumber}</h5>
                                                                <p className="card-text">{item.Description}</p>
                                                                <p className="card-text">Durata: {item.Duration}''</p>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    </div>
                                                </li>
                                            ))}
                    <div>
                        <ul className="list-group mt-n0 ">
                            {this.context.state.username?
                                <li className="list-group-item list-group-item-dark">
                                
                                    <div className="col-sm">
                                        <Rating id="rate" size="small" className="movie-rating" name="doesRating" value={this.state.myrating} onChange={(event, newValue) => {this.changeRating(event, newValue);}} />  
                                    </div>
                                    
                                </li>
                            :null}
                            {this.context.state.username?
                            <li className="list-group-item mt-n3 list-group-item-dark">
                                <div className="input-group">
                                <textarea className="form-control" aria-label="With textarea" placeholder="Write your review!" onChange={(event => this.changeReview(event))}></textarea>
                                </div>
                            </li>
                            :null}
                            {this.context.state.username?
                            <li className="list-group-item mt-n3 list-group-item-dark">
                                <button id="sendRev" type="button" className="btn btn-primary btn-sm" onClick={(e) => this.sendReview(e)}>Send</button>
                            </li>  
                            :null}
                            {this.state.reviews.map(item => (
								<div key={"review"+item.fkUsers}>                            
									<li className="commento list-group-item list-group-item-dark">
										<p>
											<span>{item.fkUsers}</span>
											<Rating size="small" className="movie-rating" name="doesRating" value={item.Rating} disabled={item.rating===null} readOnly/>
										</p>
									<span>{item.Review}</span></li>
								</div>       
                            ))}
                        </ul>
                    </div>
                </div>
        )
    }
}

MovieDetails.contextType = MContext;