import './Login.css';
import React from 'react';
import Axios from 'axios';
import {withRouter} from 'react-router-dom'
import { MContext } from './../ContextProvider/ContextProvider';


class Register extends React.PureComponent {

    constructor() {
        super();
        this.state = {
            form: {
                username: null,
                password: null
            },
            serverError: {
                hasError: false,
                errorMessage: null
            }
        }
    }

    async handleSubmit(event) {
        event.preventDefault();
        
        this.setState({serverError: {}});
        Axios
            .get("http://localhost:5000/notflix/login", {
                headers: {
                    authorization: 'Basic ' + btoa( this.state.form.username + ':' + this.state.form.password),
                    'Content-Type': 'application/json',
                },
                params: this.state.form
            })
            .then(res => {
                this.context.setStateByName("username", this.state.form.username);                
                localStorage.setItem('notflix_token', res.data.token );
                this.props.history.push("/");
            })
            .catch(err => {
                let message;
                switch(err.response.status) {
                    case 401:
                        message = "Wrong username or password";
                        break;
                    case 500:
                        message = "Internal server error";
                        break;
                    default:
                        message = "Sorry, there was a problem";
                        break;
                }

                this.setState({
                    serverError: {
                        hasError: true,
                        errorMessage: message
                    }
                });
            });
    }

    handleFormChange(field, event) {
        let form = this.state.form;
        form[field] = event.target.value
        this.setState({form: form});
    }

    render() {
        return <div id="login">
        <div className="card">
            <div className="card-body">
                <form className="form-signup">
                    <h1 className="h3 mb-3 font-weight-normal">Enter Account</h1>

                    <div className={`alert alert-danger ${this.state.serverError.hasError ? '' : 'd-none'}`} role="alert">
                        {this.state.serverError.errorMessage}
                    </div>

                    <label htmlFor="inputUsername" className="sr-only">Name</label>
                    <div className="input-group mb-2">
                        <div className="input-group-prepend">
                            <div className="input-group-text">
                                <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-person-circle" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                    <path d="M13.468 12.37C12.758 11.226 11.195 10 8 10s-4.757 1.225-5.468 2.37A6.987 6.987 0 0 0 8 15a6.987 6.987 0 0 0 5.468-2.63z"/>
                                    <path fillRule="evenodd" d="M8 9a3 3 0 1 0 0-6 3 3 0 0 0 0 6z"/>
                                    <path fillRule="evenodd" d="M8 1a7 7 0 1 0 0 14A7 7 0 0 0 8 1zM0 8a8 8 0 1 1 16 0A8 8 0 0 1 0 8z"/>
                                </svg>
                            </div>
                        </div>
                        <input type="text" id="inputUsername" className="form-control" placeholder="Username" required onChange={(event) => this.handleFormChange("username" ,event)}/>
                    </div>

                    <label htmlFor="inputPassword" className="sr-only">Password</label>
                    <div className="input-group mb-2">
                        <div className="input-group-prepend">
                            <div className="input-group-text">
                                <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-lock-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                    <path d="M2.5 9a2 2 0 0 1 2-2h7a2 2 0 0 1 2 2v5a2 2 0 0 1-2 2h-7a2 2 0 0 1-2-2V9z"/>
                                    <path fillRule="evenodd" d="M4.5 4a3.5 3.5 0 1 1 7 0v3h-1V4a2.5 2.5 0 0 0-5 0v3h-1V4z"/>
                                </svg>
                            </div>
                        </div>
                        <input type="password" id="inputPassword" className="form-control" placeholder="Password" required onChange={(event) => this.handleFormChange("password" ,event)}/>
                    </div>
                    
                    <button className="btn btn-success btn-block" type="submit" onClick={(event) => this.handleSubmit(event)}>Login</button>
                </form>
            </div>
        </div>
    </div>
    }
}

Register.contextType = MContext;
export default withRouter(Register);