import '../css/bootstrap.min.css';
import './SearchBar.css';
import React from 'react';
import axios from 'axios';
import { MContext } from '../ContextProvider/ContextProvider.js';

export default class SearchBar extends React.PureComponent {

    async movieSearch(params) {
        let url="http://localhost:5000/notflix"+
            (this.context.state.searchFavorites?"/favourites/"+this.context.state.username:"");
        let response = await axios.get(url, {
            headers: {'Content-Type': 'application/json'},
            params:params        
            });            
        this.setState({ searchResult: response.data});        
    }

    componentDidUpdate(){                
        if(this.context.state.shouldSearch){            
            this.context.setStateByName("shouldSearch",false);            
            this.handleSubmit({type:"externalUpdate"})
        }              
    }

    constructor() {
        super();
        this.state = {
            searchTerm: "",            
            searchResult:[],                        
        }
    }

    async handleSubmit(event){
        if(event.type==="submit"){            
            event.preventDefault();
        }
        let params={};        
        if(this.state.searchTerm){
            params.title=this.state.searchTerm;
        }
        if(this.context.state.selectedCategories.length>0){
            params.category=this.context.state.selectedCategories[0]
        } 

        if(Object.keys(params).length===0){
            this.context.setStateByName("reloadAll",true);
            return;
        }
        await this.movieSearch(params);        
        this.context.setStateByName("movies",this.state.searchResult);                
        this.context.setStateByName("message",this.state.searchTerm);
        this.setState({searchTerm: ""});
    }
    
    handleChange(e){        
        this.setState({searchTerm: e.target.value});
    }

    render() {              
                   
        return(       
            
            <form className="form-inline w-75 my-2 my-lg-0" onSubmit={(event) => this.handleSubmit(event)}>
                <input className="form-control w-75 mr-sm-2" type="search" placeholder="Search by Exact Title"aria-label="Search" value={this.state.searchTerm} onChange={(event) => this.handleChange(event)}/>
                <button className="btn my-2 my-sm-0" type="submit">Search</button>
            </form>
            
        )}       
     
}
SearchBar.contextType = MContext;
