import '../css/bootstrap.min.css';
import './MovieCatalog.css';
import React from 'react';
import axios from 'axios';
import {Link} from "react-router-dom";
import Rating from '@material-ui/lab/Rating';
import { MContext } from '../ContextProvider/ContextProvider.js';
import { ReactComponent as Logo } from '../logo.svg'//import logo from '../logo.svg';
export default class MovieCatalog extends React.PureComponent {
    
    constructor() {
        super();
        this.state = {willLoad:false}
    }

    async loadMovies() {
        let url="http://localhost:5000/notflix"+
            (this.props.isFavorites?"/favourites":"");
        let response = await axios.get(url,{
            headers: {
                authorization: "Bearer " + localStorage.getItem('notflix_token'),
                'Content-Type': 'application/json',
            }            
            });                
        this.context.setStateByName("movies",response.data);
        if(this.props.isFavorites){this.loadRecomended()}
    }

    async loadRecomended() {
		this.context.setStateByName("recommendedMovies",[])
        let url="http://localhost:5000/notflix/recommend";            
        let response = await axios.get(url,{
            headers: {
				authorization: "Bearer " + localStorage.getItem('notflix_token'),
                'Content-Type': 'application/json',
            }            
            });                
        if(response.data!==false){        
        this.context.setStateByName("recommendedMovies",response.data);       
        }else{
            this.setState({willLoad:false})
        }
    }


    async componentDidMount() {
        if(this.props.isFavorites){
            this.context.setStateByName("searchFavorites",true);
            this.setState({willLoad:true})         
        } else {
            this.context.setStateByName("searchFavorites",false);
            this.setState({willLoad:false})  
        }
        this.loadMovies();                
    }

    componentDidUpdate(prevProps){        
        if(this.context.state.reloadAll){            
            this.context.setStateByName("reloadAll",false);
            this.context.setStateByName("message","");
            this.loadMovies();
        }
        if(this.props.isFavorites!==prevProps.isFavorites){
            this.loadMovies();         
        }        
    }

    
    render() {        
        return(                             
            <div className={"container-catalog"+" lower".repeat(this.context.state.openDropDown)}>
                {this.props.isFavorites?<h2 className="fav-results">{"Your Favorite Movies of all Time:"}</h2>:""}
                {this.context.state.message?<h2 className="search-results">{"Searching for '"+ this.context.state.message+"' produced the following results: "}</h2>:""}
                <h2 className="genre-results">{this.context.state.selectedCategories.length>0?("In Categories: '"+ this.context.state.selectedCategories.join(" ,")+" '"):"In All Categories"}</h2>                        
                <div className="list-wrapper">{this.context.state.movies.map(movie =>
                    <div className="row-movie-wrapper" key={movie.IdMovie}>
                        <Link className="item-movie-wrapper" to={"/movie/"+movie.IdMovie}>               
                            <img   img className="movie-thumbnail" alt="" src={movie.ThumbnailURL} ></img>                            
                            <span className="info-wrapper">
                                <span className="movie-title">{movie.Title}</span>
                                <Rating size="small" className="movie-rating" name="doesRating" precision={0.5} value={movie.MeanRating} disabled={movie.MeanRating===null} readOnly />
                            </span>
                        </Link>
                    </div>
                    )}  
                </div>
                {this.state.willLoad?<h2 className="recommendation-results">{"We would like to suggest you the following Movies:"}</h2>:""}                
                {this.state.willLoad && this.context.state.recommendedMovies.length>0?
                <div className="list-wrapper">{this.context.state.recommendedMovies.map(movieRec =>
                    <div className="row-movie-wrapper" key={movieRec.IdMovie}>
                        <Link className="item-movie-wrapper" to={"/movie/"+movieRec.IdMovie}>               
                            <img   img className="movie-thumbnail" alt="" src={movieRec.ThumbnailURL} ></img>                            
                            <span className="info-wrapper">
                                <span className="movie-title">{movieRec.Title}</span>
                                <Rating size="small" className="movie-rating" name="doesRating" precision={0.5} value={movieRec.MeanRating} disabled={movieRec.MeanRating===null} readOnly />
                            </span>
                        </Link>
                    </div>
                    )}  
                </div>:
                <div>{this.state.willLoad?
                    <div>
                        <Logo className="App-logo" alt="logo" />{/* <img src={Logo} */}
                        <p>...Loading...</p>
                    </div>                
                    :""}
                </div>               
                }
            </div>      
    )}
}
MovieCatalog.contextType = MContext;
