import './CategoryDropDown.css';
import React from 'react';
import onClickOutside from "react-onclickoutside";
import axios from 'axios';

import { MContext } from '../ContextProvider/ContextProvider.js';

class CategoryDropDown extends React.PureComponent {

    async loadCategories() {
        let response = await axios.get('http://localhost:5000/notflix/categories', {
            headers: {
                'Content-Type': 'application/json',
            }            
            });
        response.data.map(item=>{return item["selected"]=false;});      
        this.setState({ categories: response.data});
    }

    async componentDidMount() {
        this.loadCategories();
        this.context.setStateByName("lowerIndex",false);
    }

    constructor() {
        super();
        this.state = {
            headerTitle:"All Categories",
            categories:[{Name:"",selected:false}]
        }
    }

    handleClickOutside(){       
        this.context.setStateByName("openDropDown",false);
      }
    
    async toggleList(){            
        this.context.setStateByName("openDropDown",!this.context.state.openDropDown);                     
    }

    toggleSelected(id){                    
        let temp=this.state.categories.find(obj => {
            return obj.Name ===id;
          })

        //Remove this lines to allow multiple selection
        let aux=!temp.selected;
        this.state.categories.map(cat=>{return cat.selected=false;});        
        temp.selected = aux;
        //Enable this lines to allow multiple selection          
        // temp.selected = !temp.selected;

        this.context.setStateByName("openDropDown",false);       
        
        this.context.setStateByName("selectedCategories",this.state.categories
                                                                            .filter((cat)=>{return cat.selected})
                                                                            .map((cat)=>{return cat.Name}))
        this.context.setStateByName("shouldSearch",true);
        }
    
    componentDidUpdate(){        
        if(this.context.state.reloadAll){                      
            this.state.categories.map(cat=>{return cat.selected=false;});      
        }   
    }


    render() {        
        const{headerTitle,categories} = this.state;
        const listOpen= this.context.state.openDropDown;
        
        return(
            <div className="dd-wrapper">
                <div className={"dd-header my-2 my-sm-0 btn "+"open".repeat(listOpen)} onClick={() => this.toggleList()}>
                    <div className="dd-header-title">{headerTitle}</div>
                </div>
                {listOpen && <div className="dd-list">
                    {categories.map((item) => (
                        <div className={"dd-list-item "+"selected".repeat(item.selected)} key={item.Name}>
                            <button className="btn" onClick={() => this.toggleSelected(item.Name)}>{item.Name}</button>
                        </div>
                    ))}
                </div>}
            </div>
    )}
}
CategoryDropDown.contextType = MContext;
export default onClickOutside(CategoryDropDown);